

















import Vue from 'vue';
import { setTimeout } from 'timers';

export default Vue.extend({
  name: 'HelloWorld',
  props: {
    text: String,
    author: String,
    fetching: Boolean,
    next: Function,
  },
  computed: {
    tweet(): string {
      return `https://twitter.com/intent/tweet?hashtags=quotes,simpsons&text="${
        this.text
      }%0A-${this.author}"`;
    },
  },
});
