




import Vue from 'vue';
import Quote from './components/Quote.vue';

export default Vue.extend({
  name: 'app',
  components: {
    Quote,
  },
  data() {
    return {
      author: '',
      quote: '',
      fetching: false,
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      const url = 'https://thesimpsonsquoteapi.glitch.me/quotes';
      this.fetching = true;
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          this.author = data[0].character;
          this.quote = data[0].quote;
          this.fetching = false;
        });
    },
  },
});
